.pet-card {
  width: 270px;
  height: 435px;
  border-radius: 9px;
  background-color: var(--color-light-s);
  padding-bottom: 30px;
  overflow: hidden;
  transition: 0.2s;
  cursor: pointer;

  &:hover {
    background-color: var(--color-light-xl);
    box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);

    .pet-card__button {
      background-color: var(--color-primary-light);
      border-color: var(--color-primary-light);
    }
  }

  &__img-container {
    width: 100%;
    height: 270px;
    overflow: hidden;

    img {
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    margin: 30px 0;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.06em;
    color: var(--color-dark-l);
    text-align: center;
  }

  &__button {
    margin: 0 auto;
  }
}